  content: "";
  top: calc(75% + 4px);
  width: 70%;
  left: 15%;
}
#mainNav ul li button:hover::before, #mainNav ul li button:hover::after {
  opacity: 1;
  transform: translate3d(0, 0, 0);
  transition-timing-function: cubic-bezier(0.2, 0, 0.3, 1);
}
#mainNav ul li button:hover::after {
  transition-delay: 0.1s;
